import React from 'react';
import { formatDate } from '@/utils/format';
import { Cotacao } from '@/services/api/cotacao.service';
import { PencilIcon, TrashBinIcon } from '@/icons';
import Button from '../ui/Button';
import Badge from '../ui/Badge';
import ButtonCard from '../ui/button/ButtonCard';

type CotacaoCardProps = {
  cotacao: Cotacao;
  onEdit: () => void;
  onDelete: () => void;
  onViewItems: () => void;
};

const statusColors = {
  EM_ANDAMENTO: 'success',
  FECHADA: 'warning',
  CANCELADA: 'error'
} as const;
const tipoColors = {
  PROGRAMADA: 'info',
  IMEDIATA: 'warning',
  // CANCELADA: 'error'
} as const;

const CotacaoCard: React.FC<CotacaoCardProps> = ({
  cotacao,
  onEdit,
  onDelete,
  onViewItems
}) => {
  return (
    <div 
    // className="bg-white dark:bg-gray-800 rounded-lg shadow-sm border border-gray-200 dark:border-gray-700">
    className="bg-white dark:bg-gray-800 rounded-lg shadow-sm hover:shadow-md shadow-gray-200 dark:shadow-gray-700 border border-gray-200 dark:border-gray-700 p-4">
      <div>
        <div className="flex justify-between items-start mb-3">
          <div>
            <div className="flex items-center gap-2 flex-wrap overflow-x-hidden">
              <h3 className="font-medium text-gray-900 dark:text-white">
                {cotacao.nome}
              </h3>
              <Badge
                variant={statusColors[cotacao.status]}
                className="capitalize"
              >
                {cotacao.status.toLowerCase().replace('_', ' ')}
              </Badge>
              <Badge
                variant={tipoColors[cotacao.tipo]}
                className="capitalize"
              >
                {cotacao.tipo.toLowerCase().replace('_', ' ')}
              </Badge>
            </div>
            <p className="text-sm text-gray-500 dark:text-gray-400 mt-1">
              {cotacao.descricao || 'Sem descrição'}
            </p>
            <p className="text-sm text-gray-500 dark:text-gray-400 mt-1">
              Abertura: {formatDate(cotacao.dataAbertura)} |
              Encerramento: {formatDate(cotacao.dataFechamento)}
            </p>
          </div>
          <div className="flex gap-2">
            <ButtonCard
              variant="secondary"
              size="xs"
              onClick={onViewItems}
              title="Ver Itens"
              icon="eye"
            />
            <ButtonCard
              variant="secondary"
              size="xs"
              onClick={onEdit}
              title="Editar Item"
              icon="edit"
            />
            <ButtonCard
              title="Excluir Item"
              icon="delete"
              variant="secondary"
              size="xs"
              onClick={onDelete}
            />
          </div>
        </div>

        <div className="space-y-2">
          {/* <p className="text-sm text-gray-600 dark:text-gray-300 line-clamp-2">
            {cotacao.observacoes || 'Sem observações'}
          </p> */}

          <div className="pt-2 border-t border-gray-100 dark:border-gray-700">
            <div className="flex justify-between items-center">
              <span className="text-sm text-gray-500 dark:text-gray-400">
                Total de Itens:
              </span>
              <span className="text-sm font-medium text-gray-900 dark:text-white">
                {cotacao.itens?.length || 0}
              </span>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default CotacaoCard;
