import React, { useState } from 'react';
import { toast } from 'react-hot-toast';
import Input from '@/components/form/input/InputField';
import Label from '@/components/form/Label';
import Button from '@/components/ui/Button';
import { Cotacao, CreateCotacaoDTO, TipoCotacao, StatusCotacao } from '@/services/api/cotacao.service';
import { formatDate } from '@/utils/format';
import Select, { SelectOption } from '../ui/Select';
import DatePicker from '../form/date-picker';

type FormErrors = {
  nome?: string;
  descricao?: string;
  dataAbertura?: string;
  dataFechamento?: string;
  tipo?: string;
  status?: string;
};

type CotacaoFormProps = {
  cotacao?: Cotacao | null;
  onSubmit: (data: CreateCotacaoDTO) => Promise<void>;
  onCancel: () => void;
};

const status = [
  { value: 'EM_ANDAMENTO', label: 'Em Andamento' },
  { value: 'FECHADA', label: 'Fechada' }
]
const tipo = [
  { value: 'IMEDIATA', label: 'Imediata' },
  { value: 'PROGRAMADA', label: 'Programada' }
];

const CotacaoForm: React.FC<CotacaoFormProps> = ({
  cotacao,
  onSubmit,
  onCancel
}) => {
  const [formData, setFormData] = useState<CreateCotacaoDTO>({
    nome: cotacao?.nome || '',
    descricao: cotacao?.descricao || null,
    dataAbertura: new Date(),
    dataFechamento: cotacao?.dataFechamento || null,
    status: cotacao?.status || 'EM_ANDAMENTO',
    tipo: cotacao?.tipo || 'IMEDIATA',
  });
  const [errors, setErrors] = useState<FormErrors>({});
  const [isSubmitting, setIsSubmitting] = useState(false);

  const validateForm = () => {
    const newErrors: FormErrors = {};
    const required = ['nome', 'dataAbertura', 'tipo', 'status'];

    required.forEach(field => {
      if (!formData[field as keyof typeof formData]) {
        newErrors[field as keyof FormErrors] = `${field.charAt(0).toUpperCase() + field.slice(1)} é obrigatório`;
      }
    });

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    if (!validateForm()) return;

    setIsSubmitting(true);
    try {
      await onSubmit(formData);
    } catch (error) {
      toast.error('Erro ao salvar cotação');
    } finally {
      setIsSubmitting(false);
    }
  };

  return (
    <form onSubmit={handleSubmit} className="space-y-6">
      <div>
        <Label htmlFor="nome">
          Nome da Cotação<span className="text-error-500">*</span>
        </Label>
        <Input
          id="nome"
          name="nome"
          value={formData.nome}
          onChange={(e) => setFormData({ ...formData, nome: e.target.value })}
          error={errors.nome}
        />
      </div>
      <div>
        <Label htmlFor="descricao">Descrição</Label>
        <Input
          id="descricao"
          name="descricao"
          value={formData.descricao || ''}
          onChange={(e) => setFormData({ ...formData, descricao: e.target.value })}
          error={errors.descricao}
        />
      </div>
      <div>
        <Label htmlFor="dataAbertura">
          Data Prevista de Abertura<span className="text-error-500">*</span>
        </Label>
        <DatePicker
          id='dataAbertura'
          onChange={(selectedDates: Date[]) => {
            if (selectedDates.length > 0) {
              setFormData({
                ...formData,
                dataAbertura: selectedDates[0]
              });
            }
          }}
          // label="Selecione a data"
          defaultDate={formData.dataAbertura ? new Date(formData.dataAbertura) : undefined}
        />
        <span>
          {errors.dataAbertura && <span className="text-error-500">{errors.dataAbertura}</span>}
        </span>
      </div>
      <div>
        <Label htmlFor="dataFechamento">
          Data Prevista de Encerramento{/* <span className="text-error-500">*</span> */}
        </Label>
        <DatePicker
          id='dataFechamento'
          onChange={(selectedDates: Date[]) => {
            if (selectedDates.length > 0) {
              setFormData({
                ...formData,
                dataFechamento: selectedDates[0]
              });
            }
          }}
          // label="Selecione a data"
          defaultDate={formData.dataFechamento ? new Date(formData.dataFechamento) : undefined}
        />
        <span>
          {errors.dataFechamento && <span className="text-error-500">{errors.dataFechamento}</span>}
        </span>
      </div>

      <div>
        <Label htmlFor="tipo">
          Tipo de Cotação<span className="text-error-500">*</span>
        </Label>
        <Select
          id="tipo"
          name="tipo"
          value={tipo.find(tipo => tipo.value === formData.tipo)}
          options={tipo}
          onChange={(option) => setFormData({ ...formData, tipo: (option as SelectOption).value as TipoCotacao })}
          isSearchable
          placeholder="Selecione o tipo"
          error={errors.tipo}
          className="w-full"
        />
      </div>
      <div>
        <Label htmlFor="status">
          Status da Cotação<span className="text-error-500">*</span>
        </Label>
        <Select
          id="status"
          name="status"
          value={status.find(status => status.value === formData.status)}
          options={status}
          onChange={(option) => setFormData({ ...formData, status: (option as SelectOption).value as StatusCotacao })}
          isSearchable
          placeholder="Selecione o status"
          error={errors.status}
          className="w-full"
        />
      </div>

      <div className="flex justify-end space-x-4 pt-4">
        <Button
          variant="secondary"
          onClick={onCancel}
          type="button"
        >
          Cancelar
        </Button>
        <Button
          variant="primary"
          type="submit"
          loading={isSubmitting}
        >
          {cotacao ? 'Atualizar' : 'Criar'}
        </Button>
      </div>
    </form>
  );
};

export default CotacaoForm;
