"use client";

import React, { useState, useEffect, useCallback } from 'react';
import { useRouter } from 'next/navigation';
import { toast } from 'react-hot-toast';
import { Cotacao, cotacaoService } from '@/services/api/cotacao.service';
import CotacaoCard from '@/components/cotacao/CotacaoCard';
import {Modal} from '@/components/ui/modal';
import CotacaoForm from '@/components/cotacao/CotacaoForm';
import Button from '@/components/ui/Button';
import Input from '@/components/form/input/InputField';
import Pagination from '@/components/ui/Pagination';
import Label from '@/components/form/Label';

export default function CotacoesPage() {
  const router = useRouter();
  const [cotacoes, setCotacoes] = useState<Cotacao[]>([]);
  const [loading, setLoading] = useState(true);
  const [searchTerm, setSearchTerm] = useState('');
  const [page, setPage] = useState(1);
  const [totalPages, setTotalPages] = useState(0);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [selectedCotacao, setSelectedCotacao] = useState<Cotacao | null>(null);
  const [itemsPerPage, setItemsPerPage] = useState(9);

  const loadCotacoes = useCallback(async () => {
    try {
      setLoading(true);
      const response = await cotacaoService.list({ 
        page, 
        limit: itemsPerPage, 
        search: searchTerm 
      });
      setCotacoes(response.data);
      // Assumindo que a API retorna informações de paginação
      if (response.pagination) {
        setTotalPages(response.pagination.pages);
      } else {
        // Fallback se não houver pagination information
        setTotalPages(Math.ceil((response.data?.length || 0) / itemsPerPage));
      }
    } catch (error) {
      toast.error('Erro ao carregar cotações');
    } finally {
      setLoading(false);
    }
  }, [page, searchTerm, itemsPerPage]);

  useEffect(() => {
    loadCotacoes();
  }, [loadCotacoes]);

  const handleEdit = (cotacao: Cotacao) => {
    setSelectedCotacao(cotacao);
    setIsModalOpen(true);
  };

  const handleDelete = async (id: number) => {
    if (!window.confirm('Tem certeza que deseja excluir esta cotação?')) return;

    try {
      await cotacaoService.delete(id);
      toast.success('Cotação excluída com sucesso');
      loadCotacoes();
    } catch (error) {
      toast.error('Erro ao excluir cotação');
    }
  };

  const handleViewItems = (cotacaoId: number) => {
    router.push(`/dashboard/empresa/produtos/cotacoes/${cotacaoId}`);
  };

  const handleSave = async (data: any) => {
    try {
      if (selectedCotacao) {
        await cotacaoService.update(selectedCotacao.id, data);
        toast.success('Cotação atualizada com sucesso');
      } else {
        await cotacaoService.create(data);
        toast.success('Cotação criada com sucesso');
      }
      setIsModalOpen(false);
      setSelectedCotacao(null);
      loadCotacoes();
    } catch (error) {
      toast.error('Erro ao salvar cotação');
    }
  };

  return (
    <div className="p-4 sm:p-6 lg:p-8">
      <div className="mb-8">
        <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4">
          <div>
            <h1 className="text-2xl font-semibold text-gray-900 dark:text-white">
              Cotações
            </h1>
            <p className="mt-1 text-sm text-gray-500 dark:text-gray-400">
              Gerencie suas Cotações
            </p>
          </div>
          <Button
            variant="primary"
            onClick={() => {
              setSelectedCotacao(null);
              setIsModalOpen(true);
            }}
          >
            Nova Cotação
          </Button>
        </div>

        <div className="mt-6">
          <div className="flex flex-col sm:flex-row gap-4 items-center justify-between mb-6">
            <Input
              placeholder="Buscar cotações..."
              value={searchTerm}
              onChange={(e) => {
                setSearchTerm(e.target.value);
                setPage(1);
              }}
              className="max-w-xs"
            />
          </div>

          <div className="flex justify-between mb-4 gap-2 flex-wrap">
            <div className='flex items-center gap-0.5'>
              <Label className='flex items-center mb-0 h-full'>Quantidade por página</Label>
              <Input
                className='max-w-24'
                defaultValue={itemsPerPage}
                max={100}
                min={1}
                onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
                  const newValue = Number(e.target.value) || 9;
                  setItemsPerPage(newValue);
                  setPage(1);
                }}
              />
            </div>
            <Pagination
              currentPage={page}
              totalPages={totalPages}
              onPageChange={setPage}
            />
          </div>
        </div>
      </div>

      {loading ? (
        <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-3">
          {[1, 2, 3].map((n) => (
            <div key={n} className="animate-pulse bg-gray-100 dark:bg-gray-800 rounded-lg h-48" />
          ))}
        </div>
      ) : (
        <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-3">
          {cotacoes.length > 0 ? (
            cotacoes.map((cotacao) => (
              <CotacaoCard
                key={cotacao.id}
                cotacao={cotacao}
                onEdit={() => handleEdit(cotacao)}
                onDelete={() => handleDelete(cotacao.id)}
                onViewItems={() => handleViewItems(cotacao.id)}
              />
            ))
          ) : (
            <div className="col-span-full text-center py-12">
              <p className="text-gray-500 dark:text-gray-400">
                Nenhuma cotação encontrada.
              </p>
            </div>
          )}
        </div>
      )}

      {totalPages > 1 && (
        <div className="mt-6">
          <div className="flex justify-end mb-4 gap-2 flex-wrap">
            <Pagination
              currentPage={page}
              totalPages={totalPages}
              onPageChange={setPage}
            />
          </div>
        </div>
      )}

      <Modal
        isOpen={isModalOpen}
        onClose={() => {
          setIsModalOpen(false);
          setSelectedCotacao(null);
        }}
        className="max-w-[800px] m-4"
      >
        <div className="no-scrollbar relative w-full overflow-y-auto rounded-3xl bg-white p-4 dark:bg-gray-900 lg:p-11">
          <div className="px-2 pr-14">
            <h4 className="mb-2 text-2xl font-semibold text-gray-800 dark:text-white/90">
              {selectedCotacao ? 'Editar Cotação' : 'Novo Cotação'}
            </h4>
            <p className="mb-6 text-sm text-gray-500 dark:text-gray-400 lg:mb-7">
              {selectedCotacao 
                ? 'Atualize as informações do cotação.' 
                : 'Preencha os dados para criar um novo cotação.'
              }
            </p>
          </div>
          <CotacaoForm
            cotacao={selectedCotacao}
            onSubmit={handleSave}
            onCancel={() => {
              setIsModalOpen(false);
              setSelectedCotacao(null);
            }}
          />
        </div>
      </Modal>
    </div>
  );
}
